/* App.css */

.App {
    text-align: center;
    background-color: #f2f2f2;
    height: 100vh;
  }
  
  .App-header {
    padding: 20px;
    background-color: #282c34;
    color: white;
  }
  
  .App-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .message-container {
    max-width: 600px;
    margin: 20px auto;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: white;
    overflow-y: auto;
    height: 300px;
  }
  
  .message {
    margin-bottom: 10px;
    padding: 8px;
    background-color: #e6e6e6;
    border-radius: 5px;
  }
  
  .input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  input {
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    padding: 8px 15px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  